import React, { Component } from 'react';
import MenuBar from '../MenuBar';
import Paper from '@material-ui/core/Paper';
import QrReader from "react-qr-reader";
import { firestoreRef } from '../config/firebaseRef'
import './index.css';
import * as firebase from "firebase";
import { UserAppBar } from '../SapeRidesAppBar';

class Scanner extends Component {
  constructor(props) {
    super(props);
    this.state = {
      delay: 300,
      result: "No result",
      
    };
    this.handleScan = this.handleScan.bind(this);
  }

  postUserData(tripId) {
    //let userId = '1372333'; //Action Needed: Get User Id from Cookie
    var userId=localStorage.getItem("sapeRidesUserId");
    var fireStoreObj = firestoreRef.collection("tripsDataV3").doc(tripId);
    fireStoreObj.update({
      boardedUsers: firebase.firestore.FieldValue.arrayUnion(userId)
    }).then(() => {
      // window.location.href="/dashboard?status=boarded";
      this.props.updateToScanState({boarded: true})
    });
  }
  
  updateTripInfo(routeId) {
    let instance = this;
    firestoreRef.collection("tripsDataV3")
    .where("isOngoingTrip","==", true).where("routeID", "==", routeId)
 .get()
 .then((querySnapshot) => {
    querySnapshot.forEach((doc) =>{
          let data = doc.data();
          this.postUserData(doc.id);
   });
 })
 .catch((error) =>{
    console.log("Error getting documents: ",error);
 });
  }

  handleScan(routeId) {
    console.log('in hanfel scan')
    console.log(routeId);
    if (routeId) {
      this.updateTripInfo(routeId);
    }
  }

  handleError(err) {
    console.error(err);
  }

  render() {
    return (
      <React.Fragment>
        <div className="wrapper">
          <UserAppBar/>
          <Paper className="screen-list upcoming-tips">
            <QrReader
                delay={this.state.delay}
                onError={this.handleError}
                onScan={this.handleScan}
                style={{ width: "100%" }}
              />
          </Paper>

          <div className="boarding-details">
            <h1 className="trip-title">Trip 1 - 8:00 AM - Morning Pick Up</h1>
            <p className="trip-desc">
              E-sign your boarding and unboarding for this trip
            </p>
          </div>
        </div>
      </React.Fragment>
    );
  }
}

export default Scanner;
